// shared/schemas/invoice.schema.ts
import { z } from "zod";
import { AppReferenceSchema } from "./app.schema";
import { ChargeReferenceSchema } from "./charge.schema";
import { EndUserReferenceSchema } from "./endUser.schema";
import { InvoiceItemReferenceSchema } from "./invoiceItem.schema";
import { UserReferenceSchema } from "./user.schema";

/**
 * Base Invoice schema — defines core structure.
 */
export const InvoiceBaseSchema = z.object({
  id: z.uuid(),
  userId: z.uuid(),
  appId: z.uuid().optional(),
  endUserId: z.uuid().optional(),
  amount: z.number(),
  currency: z.string(),
  paid: z.boolean(),
  createdAt: z.date(),
  updatedAt: z.date(),
  dueDate: z.date().optional(),
  paidAt: z.date().optional(),
  deletedAt: z.date().optional(),
});

/**
 * Reference schema — lightweight version for embedding in other models.
 */
export const InvoiceReferenceSchema = InvoiceBaseSchema.pick({
  id: true,
  amount: true,
  currency: true,
  paid: true,
  createdAt: true,
});

/**
 * Detailed schema — includes relations.
 */
export const InvoiceDetailedSchema = InvoiceBaseSchema.extend({
  user: z.lazy(() => UserReferenceSchema).optional(),
  app: z.lazy(() => AppReferenceSchema).optional(),
  endUser: z.lazy(() => EndUserReferenceSchema).optional(),
  items: z.array(z.lazy(() => InvoiceItemReferenceSchema)).optional(),
  charges: z.array(z.lazy(() => ChargeReferenceSchema)).optional(),
});

export type InvoiceBase = z.infer<typeof InvoiceBaseSchema>;
export type InvoiceReference = z.infer<typeof InvoiceReferenceSchema>;
export type InvoiceDetailed = z.infer<typeof InvoiceDetailedSchema>;
