import { z } from "zod";
import type { ModelUsageInput } from "@/shared/schemas";

/** Safely extracts the first object literal inside a function call */
function extractRequestFromFn(
  fn: () => Promise<any>
): Partial<Record<string, any>> {
  const fnStr = fn.toString();

  // Match the first {...} inside parentheses
  const argsMatch = fnStr.match(/\(\s*({[\s\S]*?})\s*\)/);
  if (!argsMatch) return {};

  let argStr = argsMatch[1];

  // Remove comments
  argStr = argStr.replace(/\/\/.*$/gm, "").replace(/\/\*[\s\S]*?\*\//g, "");

  // Quote unquoted keys
  argStr = argStr.replace(/([a-zA-Z0-9_]+)\s*:/g, '"$1":');

  // Remove trailing commas
  argStr = argStr.replace(/,(\s*[}\]])/g, "$1");

  // Keep everything up to matching closing brace
  let braceCount = 0;
  let endIndex = 0;
  for (let i = 0; i < argStr.length; i++) {
    if (argStr[i] === "{") braceCount++;
    else if (argStr[i] === "}") braceCount--;
    if (braceCount === 0) {
      endIndex = i + 1;
      break;
    }
  }
  argStr = argStr.slice(0, endIndex);

  try {
    return JSON.parse(argStr) as Partial<Record<string, any>>;
  } catch (err) {
    console.warn("Failed to parse request object:", err);
    return {};
  }
}

/** Infers the AI vendor from a model string */
export function inferVendorFromModel(model: string): string {
  const m = model.toLowerCase();
  if (m.startsWith("gpt-") || m.startsWith("text-") || m.startsWith("code-"))
    return "openai";
  if (m.startsWith("claude")) return "anthropic";
  if (m.startsWith("command-") || m.startsWith("xlarge-")) return "cohere";
  if (m.startsWith("palm-") || m.startsWith("gemini-")) return "google";
  if (m.startsWith("mistral-") || m.startsWith("mix-")) return "mistral";
  if (m.startsWith("cog-")) return "cogent";
  if (m.startsWith("jurassic-") || m.startsWith("j1-")) return "ai21";
  if (m.startsWith("llama-") || m.startsWith("alpaca-")) return "llama";
  return "unknown";
}

/** Request schema */
export const RequestDataSchema = z.object({
  model: z.string(),
  modelVersion: z.string().optional(),
  requestType: z.string(),
  maxTokens: z.number().optional(),
  temperature: z.number().optional(),
  topP: z.number().optional(),
  stopSequences: z.array(z.string()).optional(),
  prompt: z.union([z.string(), z.array(z.string())]).optional(),
  messages: z
    .array(z.object({ role: z.string(), content: z.string() }))
    .optional(),
  sdkVersion: z.string().optional(),
  userAgent: z.string().optional(),
  promptCategory: z.string().optional(),
  region: z.string().optional(),
});
export type RequestData = z.infer<typeof RequestDataSchema>;

/** Response schema */
export const ResponseDataSchema = z.object({
  promptLengthTokens: z.number().optional(),
  responseLengthTokens: z.number().optional(),
  totalTokens: z.number().optional(),
  retryCount: z.number().optional(),
  errorType: z.string().optional(),
});
export type ResponseData = z.infer<typeof ResponseDataSchema>;

/**
 * Roughly estimates token count from a string or array of strings.
 */
function estimateTokens(input?: string | string[]): number {
  if (!input) return 0;
  if (Array.isArray(input))
    return input.map((p) => p.length).reduce((a, b) => a + b, 0) / 4;
  return input.length / 4; // Approx: 1 token ≈ 4 chars
}

/**
 * Wraps an SDK call and captures model usage.
 * Tries to parse function arguments if possible, and estimates tokens if usage is missing.
 */
export async function captureModelUsage<T>(
  fn: () => Promise<T>
): Promise<{ result: T; modelUsage: Partial<ModelUsageInput> }> {
  const start = Date.now();

  // Parse function arguments for metadata
  const requestPayload: Partial<RequestData> = extractRequestFromFn(fn);
  const modelName = requestPayload.model ?? "unknown";
  const vendor = inferVendorFromModel(modelName);

  try {
    const result = await fn();
    const duration = Date.now() - start;

    // Extract usage from API result or fallback to estimated tokens
    let respData: ResponseData = (result as any)?.usage ?? {};
    if (
      respData.promptLengthTokens === undefined &&
      (requestPayload.prompt || requestPayload.messages)
    ) {
      const promptTokens =
        estimateTokens(requestPayload.prompt) +
        estimateTokens(requestPayload.messages?.map((m) => m.content));
      respData = {
        promptLengthTokens: promptTokens,
        responseLengthTokens: 0,
        totalTokens: promptTokens,
      };
    }

    const modelUsage: Partial<ModelUsageInput> = {
      model: modelName,
      vendor,
      modelVersion:
        requestPayload.modelVersion ?? (result as any)?.modelVersion,
      requestType: requestPayload.requestType ?? "unknown",
      sdkVersion: requestPayload.sdkVersion,
      userAgent: requestPayload.userAgent,
      promptCategory: requestPayload.promptCategory,
      region: requestPayload.region,
      maxTokens: requestPayload.maxTokens,
      promptLengthTokens: respData.promptLengthTokens ?? 0,
      responseLengthTokens: respData.responseLengthTokens ?? 0,
      totalTokens: respData.totalTokens,
      latencyMs: duration,
      errorType: undefined,
      retryCount: respData.retryCount,
    };

    return { result, modelUsage };
  } catch (error: unknown) {
    const duration = Date.now() - start;

    const modelUsage: Partial<ModelUsageInput> = {
      model: modelName,
      vendor,
      modelVersion: requestPayload.modelVersion,
      requestType: requestPayload.requestType ?? "unknown",
      sdkVersion: requestPayload.sdkVersion,
      userAgent: requestPayload.userAgent,
      promptCategory: requestPayload.promptCategory,
      region: requestPayload.region,
      maxTokens: requestPayload.maxTokens,
      latencyMs: duration,
      errorType: (error as any)?.name ?? "Error",
      retryCount: undefined,
    };

    const msg =
      (error as any)?.message ??
      (error as any)?.statusText ??
      "Unknown API error";
    const wrappedError = new Error(`BillAI API error: ${msg}`);
    (wrappedError as any).modelUsage = modelUsage;

    throw wrappedError;
  }
}
