// shared/schemas/userPreferences.schema.ts
import { z } from "zod";
import { UserReferenceSchema } from "./user.schema";

/**
 * Base schema — core fields
 */
export const UserPreferencesBaseSchema = z.object({
  id: z.uuid(),
  userId: z.uuid(),
  timezone: z.string().optional(),
  locale: z.string().optional(),
  language: z.string().optional(),
  emailNotifications: z.boolean().default(true),
  darkMode: z.boolean().default(false),
  createdAt: z.date(),
  updatedAt: z.date(),
});

/**
 * Reference schema — minimal info for embedding elsewhere
 */
export const UserPreferencesReferenceSchema = UserPreferencesBaseSchema.pick({
  id: true,
  userId: true,
  emailNotifications: true,
  darkMode: true,
});

/**
 * Detailed schema — includes nested relations
 */
export const UserPreferencesDetailedSchema = UserPreferencesBaseSchema.extend({
  user: z.lazy(() => UserReferenceSchema).optional(),
});

// Types
export type UserPreferencesBase = z.infer<typeof UserPreferencesBaseSchema>;
export type UserPreferencesReference = z.infer<
  typeof UserPreferencesReferenceSchema
>;
export type UserPreferencesDetailed = z.infer<
  typeof UserPreferencesDetailedSchema
>;
