// apps/shared/schemas/modelUsage.schema.ts
import { z } from "zod";
import { ApiKeyReferenceSchema } from "./apiKey.schema";
import { AppReferenceSchema } from "./app.schema";
import { OrganizationReferenceSchema } from "./organization.schema";
import { UserReferenceSchema } from "./user.schema";

/**
 * SessionMetrics — latency metrics for a model usage session
 */
export const SessionMetricsSchema = z.object({
  count: z.number().nonnegative().default(0),
  avgLatencyMs: z.number().nonnegative().default(0),
  p50LatencyMs: z.number().nonnegative().default(0),
  p90LatencyMs: z.number().nonnegative().default(0),
  p99LatencyMs: z.number().nonnegative().default(0),
});
export type SessionMetrics = z.infer<typeof SessionMetricsSchema>;

/**
 * CountMetrics — success/failure count tracking
 */
export const CountMetricsSchema = z.object({
  successCount: z.number().int().nonnegative().default(0),
  failureCount: z.number().int().nonnegative().default(0),
});
export type CountMetrics = z.infer<typeof CountMetricsSchema>;

/**
 * Base schema — core fields for ModelUsage
 */
export const ModelUsageBaseSchema = z.object({
  id: z.uuid(),
  userId: z.uuid().optional(),
  organizationId: z.uuid().optional(),
  apiKeyId: z.uuid().optional(),
  appId: z.uuid().optional(),
  model: z.string(),
  vendor: z.string(),
  modelVersion: z.string().optional(),
  requestType: z.string(),
  usageCount: z.number().default(1),
  success: z.boolean().default(true),
  successCount: z.number().default(0),
  failureCount: z.number().default(0),
  avgLatencyMs: z.number().optional(),
  p50LatencyMs: z.number().optional(),
  p90LatencyMs: z.number().optional(),
  p99LatencyMs: z.number().optional(),
  latencyMs: z.number().optional(),
  throughput: z.number().optional(),
  queueTimeMs: z.number().optional(),
  errorType: z.string().optional(),
  retryCount: z.number().optional(),
  region: z.string().optional(),
  promptLengthTokens: z.number().optional(),
  responseLengthTokens: z.number().optional(),
  unitCost: z.number().optional(),
  totalCost: z.number().optional(),
  billed: z.boolean().default(false),
  discountApplied: z.number().optional(),
  planTier: z.string().optional(),
  temperature: z.number().optional(),
  maxTokens: z.number().optional(),
  totalTokens: z.number().optional(),
  topP: z.number().optional(),
  stopSequences: z.array(z.string()).optional(),
  embeddingDimension: z.number().optional(),
  imageResolution: z.string().optional(),
  sdkVersion: z.string().optional(),
  userAgent: z.string().optional(),
  language: z.string().optional(),
  sessionId: z.string().optional(),
  promptCategory: z.string().optional(),
  responseQualityScore: z.number().optional(),
  resourceConsumption: z.number().optional(),
  concurrentRequests: z.number().optional(),
  createdAt: z.date(),
  updatedAt: z.date(),
  deletedAt: z.date().nullable().optional(),
});

/**
 * Input schema — used when creating a new model usage record
 */
export const ModelUsageInputSchema = ModelUsageBaseSchema.omit({
  id: true,
  createdAt: true,
  updatedAt: true,
  deletedAt: true,
});
export type ModelUsageInput = z.infer<typeof ModelUsageInputSchema>;

/**
 * Reference schema — lightweight version for embedding in other models
 */
export const ModelUsageReferenceSchema = ModelUsageBaseSchema.pick({
  id: true,
  model: true,
  vendor: true,
  requestType: true,
  createdAt: true,
  updatedAt: true,
});

/**
 * Detailed schema — includes relations
 */
export const ModelUsageDetailedSchema = ModelUsageBaseSchema.extend({
  user: z.lazy(() => UserReferenceSchema).optional(),
  organization: z.lazy(() => OrganizationReferenceSchema).optional(),
  apiKey: z.lazy(() => ApiKeyReferenceSchema).optional(),
  app: z.lazy(() => AppReferenceSchema).optional(),
});

export type ModelUsageBase = z.infer<typeof ModelUsageBaseSchema>;
export type ModelUsageReference = z.infer<typeof ModelUsageReferenceSchema>;
export type ModelUsageDetailed = z.infer<typeof ModelUsageDetailedSchema>;
