// shared/schemas/role.schema.ts
import { z } from "zod";
import { UserReferenceSchema } from "./user.schema";

/**
 * Base schema — core Role fields
 */
export const RoleBaseSchema = z.object({
  id: z.uuid(),
  name: z.string(),
  description: z.string().optional(),
  permissions: z.string().optional(),
  createdBy: z.string().optional(),
  updatedBy: z.string().optional(),
});

/**
 * Reference schema — minimal Role info for embedding elsewhere
 */
export const RoleReferenceSchema = RoleBaseSchema.pick({
  id: true,
  name: true,
});

/**
 * Detailed schema — includes nested relations
 */
export const RoleDetailedSchema = RoleBaseSchema.extend({
  users: z.array(z.lazy(() => UserReferenceSchema)).optional(),
});

// Types
export type RoleBase = z.infer<typeof RoleBaseSchema>;
export type RoleReference = z.infer<typeof RoleReferenceSchema>;
export type RoleDetailed = z.infer<typeof RoleDetailedSchema>;
