// shared/schemas/usageLog.schema.ts
import { z } from "zod";
import { AI_FEATURES } from "../constants/features";
import { ApiKeyReferenceSchema } from "./apiKey.schema";
import { AppReferenceSchema } from "./app.schema";
import { EndUserReferenceSchema } from "./endUser.schema";
import { OrganizationReferenceSchema } from "./organization.schema";
import { UserReferenceSchema } from "./user.schema";
import { UserSessionReferenceSchema } from "./userSession.schema";

/**
 * Features enum placeholder
 */
export const FeatureEnum = z.enum(AI_FEATURES);
export type FeatureType = z.infer<typeof FeatureEnum>;
export type Feature = z.infer<typeof FeatureEnum>;

/**
 * Base schema — core fields for a usage log event
 */
export const UsageLogBaseSchema = z.object({
  id: z.uuid(),
  userId: z.string().nullable().optional(),
  organizationId: z.string().nullable().optional(),
  appId: z.string().nullable().optional(),
  endUserId: z.string().nullable().optional(),
  apiKeyId: z.string().nullable().optional(),
  feature: FeatureEnum.default("OTHER"),
  usage: z.number(),
  unitCost: z.number().nullable().optional(),
  billed: z.boolean().default(false),
  sessionId: z.string().nullable().optional(),
  createdAt: z.date(),
  openMeterReported: z.boolean().nullable().optional(),
  openMeterId: z.string().nullable().optional(),
  deletedAt: z.date().nullable().optional(),
  metadata: z.record(z.string(), z.any()).optional(),
});

/**
 * Reference schema — minimal version for lightweight embedding
 */
export const UsageLogReferenceSchema = UsageLogBaseSchema.pick({
  id: true,
  feature: true,
  usage: true,
  billed: true,
  createdAt: true,
  sessionId: true,
  appId: true,
  organizationId: true,
});

/**
 * Detailed schema — includes nested relational references
 */
export const UsageLogDetailedSchema = UsageLogBaseSchema.extend({
  user: z
    .lazy(() => UserReferenceSchema)
    .nullable()
    .optional(),
  organization: z
    .lazy(() => OrganizationReferenceSchema)
    .nullable()
    .optional(),
  apiKey: z
    .lazy(() => ApiKeyReferenceSchema)
    .nullable()
    .optional(),
  app: z
    .lazy(() => AppReferenceSchema)
    .nullable()
    .optional(),
  endUser: z
    .lazy(() => EndUserReferenceSchema)
    .nullable()
    .optional(),
  session: z
    .lazy(() => UserSessionReferenceSchema)
    .nullable()
    .optional(),
});

// Types
export type UsageLogBase = z.infer<typeof UsageLogBaseSchema>;
export type UsageLogReference = z.infer<typeof UsageLogReferenceSchema>;
export type UsageLogDetailed = z.infer<typeof UsageLogDetailedSchema>;
