// apps/shared/schemas/organizationPlan.schema.ts
import { z } from "zod";
import { OrganizationReferenceSchema } from "./organization.schema";
import { PlanReferenceSchema } from "./plan.schema";

/**
 * Base schema — core fields for OrganizationPlan
 */
export const OrganizationPlanBaseSchema = z.object({
  id: z.uuid(),
  organizationId: z.uuid().optional(),
  planId: z.uuid().optional(),
  startDate: z.date(),
  endDate: z.date().nullable().optional(),
  createdAt: z.date(),
  updatedAt: z.date(),
});

/**
 * Reference schema — minimal fields for embedding elsewhere
 */
export const OrganizationPlanReferenceSchema = OrganizationPlanBaseSchema.pick({
  id: true,
  organizationId: true,
  planId: true,
  startDate: true,
  endDate: true,
});

/**
 * Detailed schema — includes nested relations
 */
export const OrganizationPlanDetailedSchema = OrganizationPlanBaseSchema.extend(
  {
    organization: z.lazy(() => OrganizationReferenceSchema).optional(),
    plan: z.lazy(() => PlanReferenceSchema).optional(),
  }
);

export type OrganizationPlanBase = z.infer<typeof OrganizationPlanBaseSchema>;
export type OrganizationPlanReference = z.infer<
  typeof OrganizationPlanReferenceSchema
>;
export type OrganizationPlanDetailed = z.infer<
  typeof OrganizationPlanDetailedSchema
>;
