// shared/schemas/revenueSplit.schema.ts
import { z } from "zod";
import { AppReferenceSchema } from "./app.schema";
import { UserReferenceSchema } from "./user.schema";

/**
 * Base schema — core RevenueSplit fields
 */
export const RevenueSplitBaseSchema = z.object({
  id: z.uuid(),
  appId: z.uuid(),
  recipientId: z.uuid().optional(),
  percent: z.number(),
  createdAt: z.date(),
  deletedAt: z.date().optional(),
});

/**
 * Reference schema — minimal fields for embedding elsewhere
 */
export const RevenueSplitReferenceSchema = RevenueSplitBaseSchema.pick({
  id: true,
  percent: true,
});

/**
 * Detailed schema — includes nested relations
 */
export const RevenueSplitDetailedSchema = RevenueSplitBaseSchema.extend({
  app: z.lazy(() => AppReferenceSchema),
  recipient: z.lazy(() => UserReferenceSchema).optional(),
});

// Types
export type RevenueSplitBase = z.infer<typeof RevenueSplitBaseSchema>;
export type RevenueSplitReference = z.infer<typeof RevenueSplitReferenceSchema>;
export type RevenueSplitDetailed = z.infer<typeof RevenueSplitDetailedSchema>;
