// apps/shared/schemas/usagePayload.schema.ts
import { z } from "zod";
import { AI_FEATURES } from "../constants/features";
import { ModelUsageInputSchema } from "./modelUsage.schema";

/**
 * Schema for what the SDK or external clients send
 * when reporting usage events to BillAI.
 */
export const UsagePayloadSchema = z.object({
  feature: z.enum(AI_FEATURES),
  usage: z.number().min(0),
  sessionId: z.uuid().optional(),
  appId: z.string().optional(),
  endUserId: z.string().optional(),
  organizationId: z.string().optional(),
  ipAddress: z.string().optional(),
  userAgent: z.string().optional(),
  modelUsage: ModelUsageInputSchema.partial().optional(),
  metadata: z.record(z.string(), z.any()).optional(),
});

/**
 * Type used by the npm SDK and API endpoints for usage reporting.
 */
export type UsagePayload = z.infer<typeof UsagePayloadSchema>;
