// shared/schemas/app.schema.ts
import { z } from "zod";
import {
  OrganizationDetailedSchema,
  OrganizationReferenceSchema,
} from "./organization.schema";
import { UserDetailedSchema, UserReferenceSchema } from "./user.schema";

/**
 * Base schema — core fields for App
 */
export const AppBaseSchema = z.object({
  id: z.uuid(),
  name: z.string(),
  description: z.string().optional(),
  ownerId: z.string().optional(),
  organizationId: z.string().optional(),
  createdAt: z.date(),
  updatedAt: z.date(),
  deletedAt: z.date().optional(),
  isActive: z.boolean().default(true),
});

/**
 * Reference schema — minimal embedding of App
 */
export const AppReferenceSchema = AppBaseSchema.extend({
  owner: z.lazy(() => UserReferenceSchema).optional(),
  organization: z.lazy(() => OrganizationReferenceSchema).optional(),
});

/**
 * Detailed schema — includes nested full objects
 */
export const AppDetailedSchema = AppBaseSchema.extend({
  owner: z.lazy(() => UserDetailedSchema).optional(),
  organization: z.lazy(() => OrganizationDetailedSchema).optional(),
});

// ─────────────────────────────────────────────
// Types
// ─────────────────────────────────────────────
export type AppBase = z.infer<typeof AppBaseSchema>;
export type AppReference = z.infer<typeof AppReferenceSchema>;
export type AppDetailed = z.infer<typeof AppDetailedSchema>;
