// shared/schemas/invoiceItem.schema.ts
import { z } from "zod";
import { InvoiceReferenceSchema } from "./invoice.schema";

/**
 * Base InvoiceItem schema — core fields
 */
export const InvoiceItemBaseSchema = z.object({
  id: z.uuid(),
  invoiceId: z.uuid(),
  description: z.string(),
  amount: z.number(),
  quantity: z.number().default(1),
  createdAt: z.date(),
});

/**
 * Reference schema — lightweight version for embedding in other models
 */
export const InvoiceItemReferenceSchema = InvoiceItemBaseSchema.pick({
  id: true,
  description: true,
  amount: true,
  quantity: true,
});

/**
 * Detailed schema — includes relations
 */
export const InvoiceItemDetailedSchema = InvoiceItemBaseSchema.extend({
  invoice: z.lazy(() => InvoiceReferenceSchema).optional(),
});

export type InvoiceItemBase = z.infer<typeof InvoiceItemBaseSchema>;
export type InvoiceItemReference = z.infer<typeof InvoiceItemReferenceSchema>;
export type InvoiceItemDetailed = z.infer<typeof InvoiceItemDetailedSchema>;
