// apps/shared/schemas/mfa.schema.ts
import { z } from "zod";
import { UserReferenceSchema } from "./user.schema";

/**
 * Base MFA schema — core fields
 */
export const MFABaseSchema = z.object({
  id: z.uuid(),
  userId: z.uuid(),
  type: z.string(),
  secret: z.string(),
  enabled: z.boolean().default(false),
  createdAt: z.date(),
  updatedAt: z.date(),
});

/**
 * Reference schema — lightweight version for embedding
 */
export const MFAReferenceSchema = MFABaseSchema.pick({
  id: true,
  type: true,
  enabled: true,
});

/**
 * Detailed schema — includes relations
 */
export const MFADetailedSchema = MFABaseSchema.extend({
  user: z.lazy(() => UserReferenceSchema).optional(),
});

export type MFABase = z.infer<typeof MFABaseSchema>;
export type MFAReference = z.infer<typeof MFAReferenceSchema>;
export type MFADetailed = z.infer<typeof MFADetailedSchema>;
