// shared/schemas/appApiKey.schema.ts
import { z } from "zod";
import { AppReferenceSchema } from "./app.schema";
import { UserReferenceSchema } from "./user.schema";

/**
 * Base schema for AppApiKey
 * Contains the core fields for creation / updates
 */
export const AppApiKeyBaseSchema = z.object({
  appId: z.string(),
  key: z.string(),
  createdByUserId: z.string().optional(),
  expiresAt: z.date().optional(),
});

export type AppApiKeyBase = z.infer<typeof AppApiKeyBaseSchema>;

/**
 * Minimal / Reference schema for AppApiKey
 * Includes unique identifiers and timestamps
 */
export const AppApiKeyReferenceSchema = AppApiKeyBaseSchema.extend({
  id: z.uuid(),
  createdAt: z.date(),
});

export type AppApiKeyReference = z.infer<typeof AppApiKeyReferenceSchema>;

/**
 * Full / Detailed schema for AppApiKey
 * Includes nested references to related models
 */
export const AppApiKeyDetailedSchema = AppApiKeyReferenceSchema.extend({
  app: z.lazy(() => AppReferenceSchema),
  createdBy: z.lazy(() => UserReferenceSchema).optional(),
});

export type AppApiKeyDetailed = z.infer<typeof AppApiKeyDetailedSchema>;
