// shared/schemas/planFeature.schema.ts
import { z } from "zod";
import { PlanReferenceSchema } from "./plan.schema";

/**
 * Base schema — core PlanFeature fields
 */
export const PlanFeatureBaseSchema = z.object({
  id: z.uuid(),
  planId: z.uuid(),
  name: z.string(),
  limit: z.number().optional(),
  price: z.number().optional(),
  metadata: z.any().optional(),
  createdAt: z.date(),
});

/**
 * Reference schema — minimal fields for embedding elsewhere
 */
export const PlanFeatureReferenceSchema = PlanFeatureBaseSchema.pick({
  id: true,
  name: true,
});

/**
 * Detailed schema — includes nested relations
 */
export const PlanFeatureDetailedSchema = PlanFeatureBaseSchema.extend({
  plan: z.lazy(() => PlanReferenceSchema).optional(),
});

// Types
export type PlanFeatureBase = z.infer<typeof PlanFeatureBaseSchema>;
export type PlanFeatureReference = z.infer<typeof PlanFeatureReferenceSchema>;
export type PlanFeatureDetailed = z.infer<typeof PlanFeatureDetailedSchema>;
