// apps/shared/schemas/organization.schema.ts
import { z } from "zod";
import { AppReferenceSchema } from "./app.schema";
import { ModelUsageReferenceSchema } from "./modelUsage.schema";
import { UserReferenceSchema } from "./user.schema";

/**
 * Base schema — core fields for Organization
 */
export const OrganizationBaseSchema = z.object({
  id: z.uuid(),
  name: z.string(),
  domain: z.string().nullable().optional(),
  createdAt: z.date(),
  updatedAt: z.date(),
});

/**
 * Reference schema — minimal fields for embedding elsewhere
 */
export const OrganizationReferenceSchema = OrganizationBaseSchema.pick({
  id: true,
  name: true,
  domain: true,
});

/**
 * Detailed schema — includes nested relations
 */
export const OrganizationDetailedSchema = OrganizationBaseSchema.extend({
  users: z.array(z.lazy(() => UserReferenceSchema)).optional(),
  apps: z.array(z.lazy(() => AppReferenceSchema)).optional(),
  modelUsages: z.array(z.lazy(() => ModelUsageReferenceSchema)).optional(),
});

export type OrganizationBase = z.infer<typeof OrganizationBaseSchema>;
export type OrganizationReference = z.infer<typeof OrganizationReferenceSchema>;
export type OrganizationDetailed = z.infer<typeof OrganizationDetailedSchema>;

/**
 * Input schemas
 */
export const CreateOrganizationInputSchema = z.object({
  name: z.string().min(1),
  domain: z.string().optional(),
});
export type CreateOrganizationInput = z.infer<
  typeof CreateOrganizationInputSchema
>;

export const UpdateOrganizationInputSchema = z.object({
  name: z.string().min(1).optional(),
  domain: z.string().optional(),
});
export type UpdateOrganizationInput = z.infer<
  typeof UpdateOrganizationInputSchema
>;
