// shared/schemas/endUser.schema.ts
import { z } from "zod";
import { AppReferenceSchema } from "./app.schema";
import { ChargeReferenceSchema } from "./charge.schema";
import { InvoiceReferenceSchema } from "./invoice.schema";

/**
 * Base EndUser Schema — core database representation.
 */
export const EndUserBaseSchema = z.object({
  id: z.uuid(),
  appId: z.uuid(),
  externalId: z.string(),
  email: z.email().optional(),
  createdAt: z.date(),
  updatedAt: z.date(),
  deletedAt: z.date().optional(),
});

/**
 * Reference version — for lightweight embedding.
 */
export const EndUserReferenceSchema = EndUserBaseSchema.pick({
  id: true,
  appId: true,
  externalId: true,
  email: true,
});

/**
 * Detailed version — includes relational references.
 */
export const EndUserDetailedSchema = EndUserBaseSchema.extend({
  app: z.lazy(() => AppReferenceSchema).optional(),
  invoices: z.array(z.lazy(() => InvoiceReferenceSchema)).optional(),
  charges: z.array(z.lazy(() => ChargeReferenceSchema)).optional(),
  usageLogs: z.array(z.any()).optional(), // replace with UsageLogReferenceSchema when available
  eventLogs: z.array(z.any()).optional(), // replace with EventLogReferenceSchema
  usageLimits: z.array(z.any()).optional(), // replace with UsageLimitReferenceSchema
  sdkLogs: z.array(z.any()).optional(), // replace with SdkLogReferenceSchema
});

export type EndUserBase = z.infer<typeof EndUserBaseSchema>;
export type EndUserReference = z.infer<typeof EndUserReferenceSchema>;
export type EndUserDetailed = z.infer<typeof EndUserDetailedSchema>;
