// shared/schemas/user.schema.ts
import { z } from "zod";

// ─────────────────────────────────────────────
// Imports: Related Reference Schemas
// ─────────────────────────────────────────────
import { AlertReferenceSchema } from "./alert.schema";
import { ApiKeyReferenceSchema } from "./apiKey.schema";
import { AppReferenceSchema } from "./app.schema";
import { AppApiKeyReferenceSchema } from "./appApiKey.schema";
import { AuditLogReferenceSchema } from "./auditLog.schema";
import { InvoiceReferenceSchema } from "./invoice.schema";
import { MFAReferenceSchema } from "./mfa.schema";
import { ModelUsageReferenceSchema } from "./modelUsage.schema";
import { OrganizationReferenceSchema } from "./organization.schema";
import { PlanReferenceSchema } from "./plan.schema";
import { RevenueSplitReferenceSchema } from "./revenueSplit.schema";
import { ReviewReferenceSchema } from "./review.schema";
import { RoleReferenceSchema } from "./role.schema";
import { UsageLogReferenceSchema } from "./usageLog.schema";
import { UserPreferencesReferenceSchema } from "./userPreferences.schema";
import { UserSessionReferenceSchema } from "./userSession.schema";

/**
 * Base schema for User (minimal flat structure)
 * Used internally for creation, validation, or lightweight fetches.
 */
export const UserBaseSchema = z.object({
  id: z.uuid(),
  email: z.email(),
  password: z.string().optional().nullable(),
  fullName: z.string().optional().nullable(),
  phone: z.string().optional().nullable(),
  bio: z.string().optional().nullable(),
  title: z.string().optional().nullable(),

  authId: z.string(),
  provider: z.string(),

  isActive: z.boolean().default(true),
  isVerified: z.boolean().default(false),

  avatarUrl: z.string().optional().nullable(),
  locale: z.string().optional().nullable(),
  timezone: z.string().optional().nullable(),
  language: z.string().optional().nullable(),

  roleId: z.uuid().nullable().optional(),
  organizationId: z.uuid().nullable().optional(),
  planId: z.uuid().nullable().optional(),

  createdAt: z.date(),
  updatedAt: z.date(),
  deletedAt: z.date().optional().nullable(),
});

/**
 * Reference schema — used to embed user info inside other models.
 * Lightweight, no deep nesting, avoids circular dependencies.
 */
export const UserReferenceSchema = UserBaseSchema.pick({
  id: true,
  email: true,
  fullName: true,
  organizationId: true,
  roleId: true,
});

/**
 * Detailed schema — full user with nested relations and metadata.
 */
export const UserDetailedSchema = UserBaseSchema.extend({
  // ─────────── Relations ───────────
  role: z
    .lazy(() => RoleReferenceSchema)
    .optional()
    .nullable(),

  organization: z
    .lazy(() => OrganizationReferenceSchema)
    .optional()
    .nullable(),

  plan: z
    .lazy(() => PlanReferenceSchema)
    .optional()
    .nullable(),

  // ─────────── Nested Collections ───────────
  sessions: z.array(z.lazy(() => UserSessionReferenceSchema)).optional(),
  invoices: z.array(z.lazy(() => InvoiceReferenceSchema)).optional(),
  apiKeys: z.array(z.lazy(() => ApiKeyReferenceSchema)).optional(),
  auditLogs: z.array(z.lazy(() => AuditLogReferenceSchema)).optional(),
  preferences: z
    .lazy(() => UserPreferencesReferenceSchema)
    .optional()
    .nullable(),
  mfaSettings: z.array(z.lazy(() => MFAReferenceSchema)).optional(),
  reviews: z.array(z.lazy(() => ReviewReferenceSchema)).optional(),
  modelUsages: z.array(z.lazy(() => ModelUsageReferenceSchema)).optional(),
  revenueSplits: z.array(z.lazy(() => RevenueSplitReferenceSchema)).optional(),
  apps: z.array(z.lazy(() => AppReferenceSchema)).optional(),
  alerts: z.array(z.lazy(() => AlertReferenceSchema)).optional(),
  appApiKeys: z.array(z.lazy(() => AppApiKeyReferenceSchema)).optional(),
  usageLogs: z.array(z.lazy(() => UsageLogReferenceSchema)).optional(),
});

// ─────────────────────────────────────────────
// Types
// ─────────────────────────────────────────────
export type UserBase = z.infer<typeof UserBaseSchema>;
export type UserReference = z.infer<typeof UserReferenceSchema>;
export type UserDetailed = z.infer<typeof UserDetailedSchema>;
