// shared/schemas/review.schema.ts
import { z } from "zod";
import { UserReferenceSchema } from "./user.schema";

/**
 * Review status enum
 */
export const ReviewStatusEnum = z.enum(["PENDING", "APPROVED", "REJECTED"]);

/**
 * Base schema — core Review fields
 */
export const ReviewBaseSchema = z.object({
  id: z.uuid(),
  reviewerId: z.uuid(),
  resourceType: z.string(),
  resourceId: z.string(),
  status: ReviewStatusEnum,
  notes: z.string().nullable().optional(),
  createdAt: z.date(),
  updatedAt: z.date(),
});

/**
 * Reference schema — minimal fields for embedding elsewhere
 */
export const ReviewReferenceSchema = ReviewBaseSchema.pick({
  id: true,
  status: true,
  resourceType: true,
  resourceId: true,
});

/**
 * Detailed schema — includes nested relations
 */
export const ReviewDetailedSchema = ReviewBaseSchema.extend({
  reviewer: z.lazy(() => UserReferenceSchema),
});

// Types
export type ReviewBase = z.infer<typeof ReviewBaseSchema>;
export type ReviewReference = z.infer<typeof ReviewReferenceSchema>;
export type ReviewDetailed = z.infer<typeof ReviewDetailedSchema>;
