// shared/schemas/charge.schema.ts
import { z } from "zod";
import { AppReferenceSchema } from "./app.schema";
import { EndUserReferenceSchema } from "./endUser.schema";
import { InvoiceReferenceSchema } from "./invoice.schema";

/**
 * Base Charge Schema — represents the core persisted entity.
 */
export const ChargeBaseSchema = z.object({
  id: z.uuid(),
  invoiceId: z.uuid().optional(),
  endUserId: z.uuid().optional(),
  appId: z.uuid(),
  amount: z.number().nonnegative(),
  status: z.enum(["PENDING", "PAID", "FAILED", "REFUNDED"]),
  createdAt: z.date(),
  paidAt: z.date().optional(),
  deletedAt: z.date().optional(),
});

/**
 * Reference version — minimal for nested use in related entities.
 */
export const ChargeReferenceSchema = ChargeBaseSchema.pick({
  id: true,
  appId: true,
  amount: true,
  status: true,
  createdAt: true,
});

/**
 * Detailed version — full entity with expanded relational references.
 */
export const ChargeDetailedSchema = ChargeBaseSchema.extend({
  invoice: z.lazy(() => InvoiceReferenceSchema).optional(),
  endUser: z.lazy(() => EndUserReferenceSchema).optional(),
  app: z.lazy(() => AppReferenceSchema),
});

export type ChargeBase = z.infer<typeof ChargeBaseSchema>;
export type ChargeReference = z.infer<typeof ChargeReferenceSchema>;
export type ChargeDetailed = z.infer<typeof ChargeDetailedSchema>;
