// apps/shared/schemas/auditLog.schema.ts
import { z } from "zod";
import { AppReferenceSchema } from "./app.schema";
import { UserReferenceSchema } from "./user.schema";

/**
 * Base Audit Log Schema — the core DB-level model.
 */
export const AuditLogBaseSchema = z.object({
  id: z.uuid(),
  userId: z.uuid(),
  action: z.string().min(1, "Action is required."),
  details: z.string().optional(),
  createdAt: z.date(),
  appId: z.uuid().optional(),
});

/**
 * Reference version — minimal fields for relational linking.
 * Used when embedding AuditLog references in other schemas.
 */
export const AuditLogReferenceSchema = AuditLogBaseSchema.pick({
  id: true,
  action: true,
  createdAt: true,
});

/**
 * Populated (detailed) version — includes expanded relations.
 * Useful for API responses or admin dashboard views.
 */
export const AuditLogDetailedSchema = AuditLogBaseSchema.extend({
  user: z.lazy(() => UserReferenceSchema),
  app: z.lazy(() => AppReferenceSchema).optional(),
});

export type AuditLogBase = z.infer<typeof AuditLogBaseSchema>;
export type AuditLogReference = z.infer<typeof AuditLogReferenceSchema>;
export type AuditLogDetailed = z.infer<typeof AuditLogDetailedSchema>;
