// shared/schemas/alert.schema.ts
import { z } from "zod";
import { AppReferenceSchema } from "./app.schema";
import { UserReferenceSchema } from "./user.schema";

/**
 * Base schema — core fields for Alert
 */
export const AlertBaseSchema = z.object({
  id: z.uuid(),
  userId: z.uuid(),
  appId: z.uuid().optional().nullable(),
  type: z.string(),
  message: z.string(),
  triggeredAt: z.date(),
  isRead: z.boolean().default(false),
});

/**
 * Reference schema — minimal for embedding in other models
 */
export const AlertReferenceSchema = AlertBaseSchema.pick({
  id: true,
  userId: true,
  appId: true,
  type: true,
  message: true,
  triggeredAt: true,
  isRead: true,
});

/**
 * Detailed schema — includes nested relationships
 */
export const AlertDetailedSchema = AlertBaseSchema.extend({
  user: z.lazy(() => UserReferenceSchema),
  app: z
    .lazy(() => AppReferenceSchema)
    .optional()
    .nullable(),
});

// ─────────────────────────────────────────────
// Types
// ─────────────────────────────────────────────
export type AlertBase = z.infer<typeof AlertBaseSchema>;
export type AlertReference = z.infer<typeof AlertReferenceSchema>;
export type AlertDetailed = z.infer<typeof AlertDetailedSchema>;
