// shared/schemas/userSession.schema.ts
import { z } from "zod";
import { UsageLogReferenceSchema } from "./usageLog.schema";
import { UserReferenceSchema } from "./user.schema";

/**
 * Base schema — core session fields
 */
export const UserSessionBaseSchema = z.object({
  id: z.uuid(),
  userId: z.uuid(),
  expiresAt: z.date(),
  ipAddress: z.string().optional(),
  userAgent: z.string().optional(),
  createdAt: z.date(),
  updatedAt: z.date(),
});

/**
 * Reference schema — minimal info for embedding elsewhere
 */
export const UserSessionReferenceSchema = UserSessionBaseSchema.pick({
  id: true,
  userId: true,
  expiresAt: true,
  createdAt: true,
});

/**
 * Detailed schema — includes nested relations
 */
export const UserSessionDetailedSchema = UserSessionBaseSchema.extend({
  user: z.lazy(() => UserReferenceSchema).optional(),
  usageLogs: z.array(z.lazy(() => UsageLogReferenceSchema)).optional(),
});

// Types
export type UserSessionBase = z.infer<typeof UserSessionBaseSchema>;
export type UserSessionReference = z.infer<typeof UserSessionReferenceSchema>;
export type UserSessionDetailed = z.infer<typeof UserSessionDetailedSchema>;
