// shared/schemas/plan.schema.ts
import { z } from "zod";
import { OrganizationPlanReferenceSchema } from "./organizationPlan.schema";
import { PlanFeatureReferenceSchema } from "./planFeature.schema";
import { UserReferenceSchema } from "./user.schema";

/**
 * Base schema — core Plan fields
 */
export const PlanBaseSchema = z.object({
  id: z.uuid(),
  name: z.string(),
  price: z.number(),
  maxUsage: z.number().optional(),
  interval: z.string().default("monthly"),
  features: z.string().optional(),
  createdAt: z.date(),
  updatedAt: z.date(),
});

/**
 * Reference schema — minimal fields for embedding elsewhere
 */
export const PlanReferenceSchema = PlanBaseSchema.pick({
  id: true,
  name: true,
  price: true,
  maxUsage: true,
});

/**
 * Detailed schema — includes nested relations
 */
export const PlanDetailedSchema = PlanBaseSchema.extend({
  users: z.array(z.lazy(() => UserReferenceSchema)).optional(),
  organizationPlans: z
    .array(z.lazy(() => OrganizationPlanReferenceSchema))
    .optional(),
  planFeatures: z.array(z.lazy(() => PlanFeatureReferenceSchema)).optional(),
});

// Types
export type PlanBase = z.infer<typeof PlanBaseSchema>;
export type PlanReference = z.infer<typeof PlanReferenceSchema>;
export type PlanDetailed = z.infer<typeof PlanDetailedSchema>;
