// shared/schemas/apiKey.schema.ts
import { z } from "zod";
import type { API_KEY_PREFIXES } from "../constants/apiKeys";
import { ModelUsageReferenceSchema } from "./modelUsage.schema";
import { UsageLogReferenceSchema } from "./usageLog.schema";
import { UserReferenceSchema } from "./user.schema";

// Environment can be "live" or "test"
export const API_KEY_ENVIRONMENTS = {
  LIVE: "live",
  TEST: "test",
} as const;

export type ApiKeyEnvironment =
  (typeof API_KEY_ENVIRONMENTS)[keyof typeof API_KEY_ENVIRONMENTS];

/**
 * Base schema — core fields for ApiKey
 */
export const ApiKeyBaseSchema = z.object({
  id: z.uuid(),
  userId: z.uuid().nullable().optional(),
  keyPrefix: z.string(),
  hashedKey: z.string(),
  name: z.string().nullable().optional(),
  environment: z.string().default("live"),
  revoked: z.boolean().default(false),
  createdAt: z.date(),
  updatedAt: z.date(),
});

/**
 * Reference schema — minimal for embedding
 */
export const ApiKeyReferenceSchema = ApiKeyBaseSchema.pick({
  id: true,
  userId: true,
  keyPrefix: true,
  hashedKey: true,
  name: true,
  environment: true,
  revoked: true,
  createdAt: true,
  updatedAt: true,
});

/**
 * Detailed schema — includes nested relations
 */
export const ApiKeyDetailedSchema = ApiKeyBaseSchema.extend({
  scopes: z.string().nullable().optional(),
  expiresAt: z.date().nullable().optional(),
  lastUsedAt: z.date().nullable().optional(),

  user: z
    .lazy(() => UserReferenceSchema)
    .optional()
    .nullable(),
  usageLogs: z.array(z.lazy(() => UsageLogReferenceSchema)).optional(),
  modelUsages: z.array(z.lazy(() => ModelUsageReferenceSchema)).optional(),
});

// ─────────────────────────────────────────────
// Types
// ─────────────────────────────────────────────
export type ApiKeyBase = z.infer<typeof ApiKeyBaseSchema>;
export type ApiKeyReference = z.infer<typeof ApiKeyReferenceSchema>;
export type ApiKeyDetailed = z.infer<typeof ApiKeyDetailedSchema>;
// Prefix type is the values of API_KEY_PREFIXES
export type ApiKeyPrefix =
  (typeof API_KEY_PREFIXES)[keyof typeof API_KEY_PREFIXES];

// ─────────────────────────────────────────────
// Create API Key input types
// ─────────────────────────────────────────────
export const CreateApiKeyInputSchema = z.object({
  userId: z.uuid(),
  name: z.string().optional(),
  scopes: z.array(z.string()).optional(),
  environment: z.enum(["test", "live"]).default("live"),
});

export type CreateApiKeyInput = z.infer<typeof CreateApiKeyInputSchema>;
